import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class TweetService {
  constructor (
    private http:HttpClient
    ) {}

    configUrl = 'api/tweets';

    getConfig() {
      return this.http.get(this.configUrl);
    }

}


